import uniRequest from 'uni-request';
import listCell from '@/components/mix-list-cell';
export default {
	components:{listCell},
	data() {
		return {
			goodsItem: {},
			title:'退款成功',
		};
	},
	
	onLoad(options){
		console.log(JSON.parse(options.item))
		this.goodsItem = JSON.parse(options.item)
	},
    onShow(){
		 this.getDate(this.goodsItem)
	},
	methods: {
		async getDate(item){
			await uniRequest({
				url:'mobile/order/list/return/goods/user/',
				method:'get',
				headers:{
					Authorization:'JWT '+uni.getStorageSync('userInfo').token
				},
			}).then(res=>{
				console.log(res)
				res.data.forEach(ele=>{
					if(ele.sku_id === item.sku){
						this.goodsItem = ele
					}
				})
				if(this.goodsItem.service_step === 1){
					this.title = '用户提交'
				}else if(this.goodsItem.service_step === 2){
					this.title = '后台审核中'
				}else if(this.goodsItem.service_step === 3){
					this.title = '审核通过'
				}else if(this.goodsItem.service_step === 4){
					this.title = '退款中'
				}else if(this.goodsItem.service_step === 5){
					this.title = '退款完成'
				}else{
					this.title = '审核不通过'
				}
				
			}).catch(error=>{
				console.log(error)
			})
		},
		//订单状态文字和颜色
		orderStateExp(state){
			let stateTip = '',
				stateTipColor = '#fa436a';
			switch(+state){
				case 1:
					stateTip = '待付款'; break;
				case 2:
					stateTip = '待发货'; break;
				case 3:
					stateTip = '待收货'; break;
				case 4:
					stateTip = '待评价'; break;
				case 5:
					stateTip = '已完成'; break;
				case 9:
					stateTip = '订单已关闭'; 
					stateTipColor = '#909399';
					break;
					
				//更多自定义
			}
			return {stateTip, stateTipColor};
		}
	},
	
}
